@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: 'Montserrat', sans-serif;
    background: #292525;
}

button{
    font-family: 'Montserrat', sans-serif;
}

.image-logo{
    height: 300px;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    background: transparent;
}

.search-box{
    position: absolute;
    top:50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 700px;
    background: #30475e;
    padding: 10px;
    box-sizing: border-box;
    border: 1px solid black;
    box-shadow: 0 5px 10px rgb(0,0,0,.2);
    display: flex;
}

.search-input{
    padding: 10px 10px;
    width: 550px;
    box-sizing: border-box;
    box-shadow: none;
    outline: none;
    border: none;
    justify-self: stretch;
    background: white;
}

.search-select{
    height: 100%;
    box-sizing: border-box;
    box-shadow: none;
    border: none;
    background: white;
}

.search-submit{
    border: none;
    cursor: pointer;
    background: #142850;
    color: white;
    height: 100%;
}

.body{
    margin-top: 10%;
}
.lifetime-stats{
    padding: 10px;
    width: 50vw;
    border: 1px solid #30475e;
    color: white;
    border-radius: 10px 10px 10px 10px;
}

.lifetime-box{
    display: flex;
    justify-content: space-evenly;
}

.profile-image{
    height: 40%;
    width: 45%;
    border-radius: 50%;
}

.legends-box{
    padding: 10px;
    width: 700px;
    box-sizing: border-box;
    border: 1px solid #30475e;
    color: white;
    border-radius: 10px 10px 10px 10px;
}

.legend-justify{
    display: flex;
    justify-content: space-evenly;
}

.legend-image{
    height: 300px;
    width: 300px;
    background: transparent;
}

.center{
    margin: auto;
    width: 50%;
    padding: 10px
}

.legends-stats{
    padding: 10px;
    background: transparent;
}

h2{
    color: white;
}


.loading{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.errorMsg{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: white;
    text-align: center;
}

.errorButton{
    border: none;
    background: white;
    color: black;
    height: 50px;
    width: 150px;
}



